<template>
  <div class="case-template g-in">
    <div class="case-list-top clearfix">
      <!-- tabs切换 -->
      <el-tabs class="fl" v-model="codeId" @tab-click="tabClick">
        <el-tab-pane label="全部"></el-tab-pane>
        <el-tab-pane :label="item.classes" :name="String(item.classesId)" v-for="item in tabList" :key="item.classesId"></el-tab-pane>
      </el-tabs>
      <!-- 搜索 -->
      <div class="fr">
        <el-input placeholder="搜索法治案例..." v-model.trim="searchInfo" class="input-with-select" clearable>
          <el-button slot="append" class="search-btn" @click="searchData">搜索</el-button>
        </el-input>
      </div>
    </div>
   
    <div class="case-list-content">
      <ul class="list" v-if="showAll">
        <p class="noData" v-if="noDataText">抱歉，未找到您所需要的内容</p>
        <li class="clearfix" v-for="item in allNewsList" :key="item.id">
          <p class="classes-tip fl">
            <span>{{item.classes}}</span>
          </p>
          <p class="all-content fl" @click="goDetail(item)">{{ item.title }}</p>
          <p class="time fr">{{ item.publishTime&&item.publishTime.split(' ')[0] }}</p>
        </li>
      </ul>
      <ul class="list" v-else>
        <p class="noData" v-if="noDataText">抱歉，未找到您所需要的内容</p>
        <li class="clearfix" v-for="item in newsList" :key="item.id">
          <p class="content fl" @click="goDetail(item)">{{ item.title }}</p>
          <p class="time fr">{{ item.publishTime&&item.publishTime.split(' ')[0] }}</p>
        </li>
      </ul>
      <el-pagination background layout="prev, pager, next" :total="total" :current-page="pageInfo.pageNumber" @current-change="handleCurrentChange"></el-pagination>
    </div>

  </div>
</template>

<script>
import Cookies from 'js-cookie'
export default {
  data() {
    return {
      codeId: String(Cookies.get('case')),
      tabList: [],
      searchInfo: '',
      pageInfo: {
        pageNumber: 1,
        pageSize: 10
      },
      newsList: [],
      allNewsList: [],
      total: 0,
      noDataText: false,
      showAll: false
    }
  },
  created() {
    this.getNewsInfo()
  },
  beforeRouteEnter(to, from, next) {
    if(!Cookies.get('case') || from.name !== 'casedetail'){
      Cookies.set('case', 1) 
    }
    next()
  },
  methods: {
    // 请求新闻数据
    async getNewsInfo() {
      const res = await this.$ajaxPost(this.projectName + '/case/findCaseClessesList.do')
      // console.log(res)
      this.tabList = res
      this.tabList.reverse()
      this.ajaxData()
    },
    // 发送数据请求
    async ajaxData() {
      const res = await this.$ajaxPost(this.projectName + '/case/finds.do', {
        classesId: this.codeId,
        ...this.pageInfo
      })
      // console.log(res)
      this.noDataText = Array.isArray(res)
      this.newsList = res.rows
      this.total = res.total
    },
    // 请求全部数据ajax
    async ajaxAllData() {
      const res = await this.$ajaxPost(this.projectName + '/case/finds.do', this.pageInfo)
      // console.log(res)
      this.noDataText = Array.isArray(res)
      this.allNewsList = res.rows
      this.total = res.total
    },
    tabClick(tab, event) {
      this.pageInfo.pageNumber = 1
      if(!!tab.name) {
        this.showAll = false
        this.allNewsList = []
        Cookies.set('case', Number(tab.name))
        this.ajaxData()
      } else {
        this.showAll = true
        this.ajaxAllData()
      }
    },
    // 分页
    handleCurrentChange(val) {
      this.pageInfo.pageNumber = val
      if(!this.showAll){
        this.ajaxData()
      } else {
        this.ajaxAllData()
      }
    },
    // 搜索
    async searchData() {
      this.pageInfo.pageNumber = 1
      if(!this.searchInfo) {
        this.$message.warning('请输入搜索内容...')
        return
      }
      const res = await this.$ajaxPost(this.projectName + '/case/finds.do', !this.showAll ? {
        classesId: this.codeId,
        ...this.pageInfo,
        title: this.searchInfo
      } : {
        ...this.pageInfo,
        title: this.searchInfo
      })
      // console.log(res)
      this.noDataText = Array.isArray(res)
      if(!this.showAll){
        this.newsList = res.rows
      } else {
        this.allNewsList = res.rows
      }
      this.total = res.total
    },
    // 进入详情
    goDetail(item) {
      // console.log(item)
      Cookies.set('case', item.classesId)
      this.$router.push({
        name: 'casedetail',
        params: {
          id: item.id
        }
      })
    }
  }
}
</script>

<style lang="scss">
.case-template{
  min-height: 600px;
  margin: 40px auto !important;
  .case-list-top{
    margin: 0 0 20px;
    .el-tabs{
      margin-left: 5px;
      .el-tabs__nav{
        .el-tabs__item{
          font-size: 16px;
          color: #797979;
        }
        .el-tabs__item.is-active{
          color: #00aec4 !important;
        }
      }
    }
    .el-input{
      .el-input-group__append{
        background-color: #E0AD78;
        color: #fff;
        border: #E0AD78;
        border-left: none;
      }
    }
  }
  .case-list-content{
    background-color: #fff;
    border:1px solid #E4E4E4;
    box-sizing: border-box;
    padding: 10px;
    .el-breadcrumb{
      font-size: 12px;
      margin-bottom: 10px;
    }
    .list{ 
      box-sizing: border-box;
      padding: 0 30px;
      .noData{
        text-align: center;
        color: #ccc;
      }
      li{
        height: 56px;
        border-bottom: 1px solid #E8E8E8;
        cursor: pointer;
        &:last-child{
          border-bottom: 0;
        }
        &:hover{
          background-color: #FBFBFB;
          border-radius: 4px;
        }
        &:hover p.content, &:focus p.content{
          color: #00aec4;
        }
        &:hover .content::before, &:focus .content::before{
          background-color: #00aec4 !important;
        }
        .classes-tip{
          height: 100%;
          line-height: 56px;
          margin: 0;
          font-size: 12px;
          color: #fff;
          span{
            background-color: #00aec4;
            padding: 2px 4px;
            border-radius: 2px;
          }
        }
        .content{
          width: 80%;
          height: 100%;
          margin: 0;
          line-height: 56px;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          color: #4A4A4A;
          font-size: 15px;
          box-sizing: border-box;
          padding-left: 20px;
          position: relative;
          &:before{
            content: '';
            display: block;
            width: 6px;
            height: 6px;
            background-color: #D8D8D8;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate(0, -50%);
          }
        }
        .all-content{
          width: 80%;
          height: 100%;
          margin: 0;
          line-height: 56px;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          color: #4A4A4A;
          font-size: 15px;
          box-sizing: border-box;
          padding-left: 20px;
        }
        .time{
          font-size: 12px;
          color: #797979;
          height: 100%;
          margin: 0;
          line-height: 56px;
        }
      }
    }
    .el-pagination{
      box-sizing: border-box;
      padding: 10px 20px;
      text-align: right;
    }
  }
}
</style>



