<template>
  <div class="case-details g-in">
    <!-- 面包屑 -->
    <div class="detail-bread">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item v-for="(item,index) in breadList" :key="index" :to="item.name?{name: item.name}:''">{{ item.title }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="case-detail-contents">
      <div class="detail-top">
        <h1>{{ newsDetail.title }}</h1>
        <p>发布时间：<span>{{newsDetail.publishTime&&newsDetail.publishTime.split(' ')[0]}}</span>&emsp;&emsp;&emsp;&emsp;
        来源：<span>{{ newsDetail.source }}</span>&emsp;&emsp;&emsp;&emsp;
        浏览量：<span>{{ newsDetail.hit }}</span></p>
        <div class="top-tip" v-if="classesId === '5'">
          <div class="top-tip-content g-in">
            <div>
              <p>专家名称：<span>{{newsDetail.expertName}}</span></p>
              <p>专家简介：<span>{{newsDetail.expertSummary}}</span></p>
            </div>
          </div>
          <span class="base">说案专家</span>
        </div>
      </div>
      <div class="detail-content" v-html="newsDetail.content"></div>
      <p v-if="newsDetail.editor!=='null'" class="detail-content-bottom">{{newsDetail.editor==='null'?'':'责任编辑：'+newsDetail.editor}}</p>
    </div>
    <BackTop></BackTop>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
export default {
  data() {
    return {
      breadList: this.$route.meta.bc,
      classesId: Cookies.get('case'),
      newsId: this.$route.params.id,
      newsDetail: {}
    }
  },
  created() {
    this.getRouterInfo()
  },
  methods: {
    getRouterInfo() {
      this.breadDataHandle()
      this.ajaxData()
    },
    // 请求新闻 数据
    async ajaxData() {
      const res = await this.$ajaxPost(this.projectName + '/case/find.do', {
        id: this.newsId
      })
      // console.log(res)
      this.newsDetail = res
    },
    // 面包屑数据处理
    async breadDataHandle() {
      this.breadList = this.breadList.slice(0, 1)
      const res = await this.$ajaxPost(this.projectName + '/case/findCaseClessesList.do')
      let newsInfo = res.filter(el => el.classesId === Number(this.classesId))[0]
      this.breadList.push({
        title: newsInfo.classes,
        name: 'caselist'
      })
      this.breadList.push({
        title: '详情'
      })
    }
  }
}
</script>

<style lang="scss">
.case-details{
  min-height: 600px;
  .detail-bread{
    background-color: #FFF;
    box-sizing: border-box;
    padding: 0 20px;
    height: 48px;
    margin: 50px 0 20px;
    .el-breadcrumb{
      line-height: 48px;
    }
  }
  .case-detail-contents{
    background-color: #FFF;
    box-sizing: border-box;
    padding: 35px 20px;
    margin-bottom: 80px;
    .detail-top{
      width: 100%;
      border-bottom: 1px solid #E8E8E8;
      h1{
        font-weight: 400;
        font-size: 24px;
        text-align: center;
        width: 80%;
        margin: 0 auto;
        line-height: 1.5;
      }
      p{
        margin: 30px 0;
        text-align: center;
      }
      .top-tip{
        width: 100%;
        height: 124px;
        box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.1),0px 2px 4px 0px rgba(0, 0, 0, 0.15);
        margin: 20px 0;
        position: relative;
        .top-tip-content{
          width: 80%;
          height: 100%;
          line-height: 124px;
          >div{
            // width: 32.8%;
            display: inline-block;
            vertical-align: middle;
            p{
              margin: 10px 0;
              color: #3E3E3E;
              text-align: left;
            }
          }
        }
        .base{
          position: absolute;
          top: 20px;
          left: -2px;
          padding: 8px 10px;
          color: #fff;
          background-color: #034766;
        }
      }
    }
    .detail-content,.detail-content-bottom{
      box-sizing: border-box;
      padding: 24px;
      line-height: 1.5;
    }
    .detail-content-bottom{
      text-align: right;
    }
  }
}
</style>



